.contenido {
  width: 90%;
  margin: 0 auto;
  padding: 3rem 2rem 0 2rem;
}
@media (min-width: 1024px) {
  .contenido {
    margin: 0 auto;
    width: 85%;
  }
}
@media (min-width: 768px) {
  .contenido {
    margin: 1rem auto;
    width: 62%;
  }
}

.barra {
  margin: 0 auto;
  text-align: center;
}
@media (min-width: 1024px) {
  .barra {
    display: flex;
    flex-direction: row;
    position: relative;
  }
}
.barra .buttons {
  margin-top: 2rem;
  display: flex;
  justify-content: space-around;
}
@media (min-width: 1024px) {
  .barra .buttons {
    position: absolute;
    top: 0;
    right: 0;
  }
}
.barra .buttons .boton-claro {
  background-color: #2e79ba;
  border-radius: 999px;
  box-shadow: #2e79ba 0 10px 20px -10px;
  box-sizing: border-box;
  color: #FFFFFF;
  cursor: pointer;
  font-family: Inter, Helvetica, "Apple Color Emoji", "Segoe UI Emoji", NotoColorEmoji, "Noto Color Emoji", "Segoe UI Symbol", "Android Emoji", EmojiSymbols, -apple-system, system-ui, "Segoe UI", Roboto, "Helvetica Neue", "Noto Sans", sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
  opacity: 1;
  outline: 0 solid transparent;
  padding: 8px 18px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  width: fit-content;
  word-break: break-word;
  border: 0;
  transition: background-color 0.3s ease;
  height: 4rem;
}
.barra .buttons .boton-claro:hover {
  background-color: #245e91;
}
.barra .buttons .boton-oscuro {
  background-color: #081f37;
  border-radius: 999px;
  box-shadow: #2e79ba 0 10px 20px -10px;
  box-sizing: border-box;
  color: #FFFFFF;
  cursor: pointer;
  font-family: Inter, Helvetica, "Apple Color Emoji", "Segoe UI Emoji", NotoColorEmoji, "Noto Color Emoji", "Segoe UI Symbol", "Android Emoji", EmojiSymbols, -apple-system, system-ui, "Segoe UI", Roboto, "Helvetica Neue", "Noto Sans", sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
  opacity: 1;
  outline: 0 solid transparent;
  padding: 8px 18px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  width: fit-content;
  word-break: break-word;
  border: 0;
  transition: background-color 0.3s ease;
  height: 4rem;
}
@media (min-width: 1024px) {
  .barra .buttons .boton-oscuro {
    margin-left: 2rem;
  }
}
.barra .buttons .boton-oscuro:hover {
  background-color: #02060a;
}

.informacion {
  margin: 0 auto;
}
@media (min-width: 1024px) {
  .informacion {
    display: flex;
    flex-direction: row;
  }
}

.education {
  margin-top: 4rem;
}
@media (min-width: 1024px) {
  .education {
    flex: 50%;
  }
}
.education .education-entry {
  margin-top: 2rem;
  background-color: #e4f6fd;
  border-radius: 0.5rem;
  padding-bottom: 1rem;
}
.education .education-entry .education-entry-institution {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0 1rem;
}
.education .education-entry .education-entry-degree {
  margin: 1rem 1rem;
  background-color: #FFF;
  border-radius: 0.5rem;
  padding: 1rem;
}

.skills {
  margin-top: 4rem;
  /*agregar texto con hover*/
}
@media (min-width: 1024px) {
  .skills {
    flex: 50%;
  }
}
@media (min-width: 768px) {
  .skills h3 {
    margin-left: 2rem;
  }
}
.skills .skills-icons {
  width: 100%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
@media (min-width: 1024px) {
  .skills .skills-icons {
    display: grid;
    width: 100%;
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (min-width: 768px) {
  .skills .skills-icons {
    display: grid;
    width: 100%;
    grid-template-columns: repeat(3, 1fr);
  }
}
.skills .skills-type {
  text-align: center;
  margin-top: 2rem;
}
.skills .skills-icon {
  position: relative;
  display: inline-block;
}
.skills .tooltiptext {
  visibility: hidden;
  background-color: black;
  color: #FFF;
  width: 120px;
  text-align: center;
  border-radius: 6px;
  padding: 3px 0;
  position: absolute;
  z-index: 1;
  bottom: 100%;
  left: 50%;
  margin-left: -60px;
}
.skills .skills-icon:hover .tooltiptext {
  visibility: visible;
}
.skills picture {
  width: 6rem;
  margin: 2rem auto;
  transition: transform 0.3s ease;
}
.skills picture:hover {
  transform: scale(1.1);
}

